import { Component, ViewChild, Renderer2, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, merge } from 'rxjs';
import { mergeMap, map, takeUntil } from 'rxjs/operators';


@Component({
  selector: 'carousel-ui',
  templateUrl: './carousel.html',
  styleUrls: ['carousel.scss']
})
export class CarouselUi {
  @ViewChild('carouselList') carouselList: ElementRef; 
  touchStart$;
  touchMove$;
  touchEnd$;

  constructor(
    public router: Router,
    public renderer: Renderer2
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    this.touchStart$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchstart');
    this.touchMove$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchmove')
    this.touchEnd$ = Observable.fromEvent(this.carouselList.nativeElement, 'touchend')
    // Observable.fromEvent(window, 'mousemove')
    // .subscribe(event => {
    //   console.log("mousemove event :: ", event)
    // })

    // Observable.fromEvent(this.carouselList.nativeElement, 'mousedown')
    // .subscribe(event => {
    //   console.log('mousedown event :: ', event)
    // })
    this.touchEventSet();
  }

  touchEventSet(){
    this.touchStart$.pipe(
      mergeMap(start => this.touchMove$.pipe(
        takeUntil(this.touchEnd$),
        map(move => {
          return {start, move}
        })
      )),
    ).subscribe(e => {
      let startPoint = e.start.changedTouches[0].pageX;
      let movePoint = e.move.changedTouches[0].pageX;
      let sub = movePoint - startPoint
      this.translateX(this.carouselList.nativeElement, sub)
    })

  }

  translateX(element, deltaX, duration = 0, callback = null) {
    element.style.transition = 'transform ' + duration + 's';
    element.style.transform = 'translate3d(' + deltaX + 'px, 0, 0)';
    if (duration > 0 && callback) {
        element.addEventListener('transitionend', callback, {once: true})
    }
  }
}
